import { loadList, loadDetail } from '../common/invoke';
import { getAssess } from '../score/const';
import SHOW_ARROW from './images/show-arrow.png';
import HIDE_ARROW from './images/hide-arrow.png';
import PREV_ARROW from './images/prev-arrow.png';
import NEXT_ARROW from './images/next-arrow.png';
import PREV_ARROW_GRAY from './images/prev-arrow-gray.png';
import NEXT_ARROW_GRAY from './images/next-arrow-gray.png';
import DEFAULT_AVATAR from './images/default-avatar.png';
import DEFAULT_FEMALE_AVATAR from './images/default-female-avatar.jpg';

const typeMap = {
    attention: '注意力',
    memory: '记忆力',
    solution: '推理力',
    executive: '执行力',
};

const types = ['attention', 'memory', 'solution', 'executive'];

const PAGE_COUNT = 3;

const $Body = $('body');
let $History = null;
let currentIndex = 0;
let currentPage = 1;
let seriesData = [];
let xAxIsData = [];
let scores = [];
let echart = null;
let scoreTotal = 99;
let scoreList = [];
let $next = null;
let $prev = null;
let $prevImg = null;
let $nextImg = null;
const $loading = $('#loading');

const parseDate = (param, format) => {
    const date = new Date(param);
    const year = date.getFullYear();
    const month = date.getMonth() > 8 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`;
    const day = date.getDate() > 9 ? date.getDate() : `0${date.getDate()}`;
    if (format === 'date') {
        return `${year} 年 ${month} 月 ${day} 日`;
    }
    const hours = date.getHours() > 9 ? date.getHours() : `0${date.getHours()}`;
    const minutes = date.getMinutes() > 9 ? date.getMinutes() : `0${date.getMinutes()}`;
    return `${year}.${month}.${day} ${hours}:${minutes}`;
};

const setLoading = (isLoading) => {
    $loading.css('display', !isLoading ? 'none' : 'flex');
};

const getBaby = () => {
    const { name, avatar, sex } = JSON.parse(window.iBrainConfig.baby);
    let img = avatar;
    if (!avatar) {
        img = sex === 1 ? DEFAULT_AVATAR : DEFAULT_FEMALE_AVATAR;
    }
    return $(`
        <div class="baby-info" id="baby">
            <img src="${img}" alt="${name}" />
            <div class="baby-name">${name}</div>
        </div>
    `);
};

const getLastTip = ({ over, time }) => {
    const { name } = JSON.parse(window.iBrainConfig.baby);
    const percent = over >= 0.999 ? 99.9 : (Math.round(over * 1000) / 10);
    return $(`
        <div class="last-tip">
            ${!window.__examId
        ? `<div class="tip-text">
                ${parseDate(time, 'date')}评估结果&nbsp;
                ${currentIndex === 0 && currentPage === 0 ? '<span class="text-suffix">  (最新)</span>' : ''}
                `
        : `<div class="tip-title">恭喜，<span class="baby-name">${name}</span>
                    在本次评估中超过了${percent}% 的同龄孩子
                    </div>
                    <div class="exam-time">评估时间：${parseDate(time)}</div>`}
        </div>
`);
};

const getScores = ({
    total_score: totalScore,
    solution_score: solutionScore,
    memory_score: memoryScore,
    executive_score: executiveScore,
    attention_score: attentionScore,
}) => $(`
        <div class="score-wrapper">
            <div class="column column-left">
                <div class="wrapper-item score-attention"><span class="item-text">注意力</span><span class="item-score">${Math.round(attentionScore)} 分</span></div>
                <div class="wrapper-item score-solution"><span class="item-text">推理力</span><span class="item-score">${Math.round(solutionScore)} 分</span></div>
            </div>
            <div class="column column-center column-center-left">
                <div class="line line-top-left"></div>
                <div class="line line-bottom-left"></div>
            </div>
            <div class="column column-center column-center-right">
                <div class="line line-top-right"></div>
                <div class="line line-bottom-right"></div>
            </div>
            <div class="column column-right">
                <div class="wrapper-item score-memory"><span class="item-text">记忆力</span><span class="item-score">${Math.round(memoryScore)}分</span></div>
                <div class="wrapper-item score-executive"><span class="item-text">执行力</span><span class="item-score">${Math.round(executiveScore)} 分</span></div>
            </div>
            
            <span class="score-total"><span class="total-text">${Math.round(totalScore)}</span><span class="score-unit">分</span></span>
            <span class="norm-date">计分常模更新于 ${JSON.parse(window.iBrainConfig.scoreParams).normDate}</span>
        </div>
    `);
const getTotalAssessment = assessment => $(`
        <div class="total-assessment">
            ${assessment}
        </div>
    `);
const getToggle = ({
    type = '', score = '0', assessment = '', over,
}) => {
    const percent = over >= 0.999 ? 99.9 : (Math.round(over * 1000) / 10);
    const $wrapper = $('<div class="toggle-wrapper" />');
    const $head = $(`
        <div class="toggle-head">
            <div class="head-left">
                <div class="left-score">
                    <span class="score-text">${typeMap[type]}</span>
                    <span class="score-num">${Math.round(score)}分</span>
                </div>
                ${over === undefined
        ? ''
        : `<div class="head-rate">超过了${percent}%的孩子</div>`}
            </div>
        </div>
    `);
    const $btn = $('<div class="head-btn">查看详情</div>');
    const $img = $(`<img src="${SHOW_ARROW}">`);
    $btn.append($img);
    $head.append($btn);
    const $body = $(`
        <div class="toggle-body">
            
        </div>
    `);
    const $assessment = $(`
        <div class="body-assessment">
            <div class="assessment-text">${assessment}</div>
        </div>
    `);
    $body.prepend($assessment);

    $wrapper.append([$head, $body]);
    $btn.click(() => {
        if ($body.css('display') === 'none') {
            $btn.text('收起详情');
            $img.attr('src', HIDE_ARROW);
            $body.show(300);
        } else {
            $btn.text('查看详情');
            $img.attr('src', SHOW_ARROW);
            $body.hide(300);
        }
        $btn.append($img);
    });
    $body.hide();
    return $wrapper;
};

const renderDetail = (score) => {
    if (!score.__isDetail) {
        loadDetail(score.id)
            .then(({ payload: { result } }) => {
                if (result) {
                    const detail = { ...result, __isDetail: true };
                    scores.splice(currentIndex, 1, detail);
                    renderDetail(detail);
                }
            });
    }
    if (!score) {
        return $('body').html('暂无数据');
    }
    if ($History) $History.remove();
    const assess = getAssess(score);
    $History = $('<div class="history" />');
    if (!window.__examId) {
        $('#baby').remove();
        $Body.prepend(getBaby());
    }
    $History.append(getLastTip({
        over: score.total_over_percent,
        time: score.begin,
    }))
        .append(getScores(score))
        .append(getTotalAssessment(assess.total.assess))
        .append(types.map(type => getToggle({
            type,
            score: assess[type].score,
            assessment: assess[type].assess,
            over: assess[type].over,
        })));
    return $Body.append($History);
};

const updateHistory = (pageIndex) => {
    currentPage = pageIndex;
    const { length } = scoreList;
    if ((currentPage + 1) * PAGE_COUNT <= length || scoreTotal <= length) {
        currentIndex = 0;
        scores = scoreList.slice(currentPage * PAGE_COUNT, (currentPage + 1) * PAGE_COUNT);
        renderDetail(scores[currentIndex]);
        seriesData = scores.map(({ total_score: ts }) => ts);
        xAxIsData = scores.map(({ begin }) => parseDate(begin));
        const option = {
            xAxis: {
                data: xAxIsData,
            },
            series: [{ data: seriesData }, { data: seriesData }],
        };
        echart.setOption(option);
        if (currentPage === Math.ceil(scoreTotal / PAGE_COUNT) - 1) {
            $prevImg.attr('src', PREV_ARROW_GRAY);
            $prev.css('color', 'gray');
        } else {
            $prevImg.attr('src', PREV_ARROW);
            $prev.css('color', '#08aca2');
        }
        if (currentPage === 0) {
            $nextImg.attr('src', NEXT_ARROW_GRAY);
            $next.css('color', 'gray');
        } else {
            $nextImg.attr('src', NEXT_ARROW);
            $next.css('color', '#08aca2');
        }
    } else {
        setLoading(true);
        const { id } = JSON.parse(window.iBrainConfig.baby);
        return loadList(id, length, PAGE_COUNT)
            .then((res) => {
                setLoading(false);
                const { result } = res.payload;
                if (!result.total) {
                    $('body').html('数据加载错误');
                }
                scoreTotal = result.total;
                scoreList = scoreList.concat(result.scores);
                updateHistory(currentPage);
            })
            .catch(() => {
                setLoading(false);
                $('body').html('数据加载失败');
            });
    }
    return true;
};
const renderHistory = () => {
    const $echarts = $('<div id="echarts"/>');
    $Body.append($echarts);
    echart = echarts.init($echarts[0], null, { height: '256px' });
    const option = {
        animation: false,
        legend: {
            orient: 'vertical',
        },
        xAxis: {
            data: [],
            inverse: true,
            axisLabel: {
                interval: 0,
                fontSize: 14,
                fontWeight: 'bold',
                color: param => (param === xAxIsData[currentIndex] ? '#68acfb' : '#21d1c6'),
                formatter: param => param.split(' ').join('\n'),
            },
            axisLine: {
                lineStyle: {
                    type: 'solid',
                    color: '#08aca2',
                    width: '3',
                },
            },
            axisTick: {
                show: false,
            },
        },
        yAxis: {
            show: false,
        },
        series: [{
            name: '时间',
            type: 'bar',
            data: [],
            barWidth: 20,
            itemStyle: {
                normal: {
                    color: param => (param.dataIndex === currentIndex ? '#68acfb' : '#21d1c6'),
                    label: {
                        show: true,
                        position: 'top',
                        fontSize: 18,
                        fontWeight: 'bold',
                        formatter: param => (param.dataIndex === currentIndex ? `${Math.round(param.value)}分` : ''),
                    },
                },
            },
        }, {
            name: '日期',
            type: 'line',
            data: [],
            itemStyle: {
                normal: {
                    color: '#8481ff',
                },
            },
        }],
    };
    echart.setOption(option);
    echart.on('click', (e) => {
        if (e.seriesType === 'bar') {
            currentIndex = e.dataIndex;
            echart.setOption({});
            renderDetail(scores[e.dataIndex]);
        }
    });
    echart.dispatchAction({
        type: 'click',
        dataIndex: 0,
        seriesType: 'bar',
    });
    $next = $('<div class="echart-btn next-btn">最近</div>');
    $nextImg = $(`<img src="${NEXT_ARROW}" />`);
    $next.append($nextImg);
    $next.click(() => {
        if (currentPage <= 0) return;
        updateHistory(currentPage - 1);
    });
    $echarts.append($next);
    $prev = $('<div class="echart-btn prev-btn">更早</div>');
    $prev.css('z-index', '99');
    $prevImg = $(`<img src="${PREV_ARROW}" />`);
    $prev.prepend($prevImg);
    $prev.click(() => {
        if (currentPage >= Math.ceil(scoreTotal / PAGE_COUNT) - 1) return;
        updateHistory(currentPage + 1);
    });
    $echarts.prepend($prev);
};

const parseSearch = (search) => {
    if (!search) return {};
    const result = {};
    const target = search.slice(1);
    target.split('&').forEach((item) => {
        const kv = item.split('=');
        result[kv['0']] = kv['1'];
    });
    return result;
};

export {
    renderHistory,
    renderDetail,
    updateHistory,
    setLoading,
    parseSearch,
};



