
import { loadDetail } from './common/invoke';
import { renderDetail, renderHistory, updateHistory, setLoading, parseSearch } from './assessment/render';


import './assessment/assessment.css';

const timer = setInterval(() => {
    if (!window.iBrainConfig) return;
    clearInterval(timer);
    const { id: examId } = parseSearch(window.location.search);
    if (!examId) {
        renderHistory();
        updateHistory(0);
    } else {
        window.__examId = examId;
        setLoading(true);
        loadDetail(examId)
            .then((res) => {
                setLoading(false);
                renderDetail({ ...res.payload.result, __isDetail: true });
            })
            .catch(() => {
                setLoading(false);
                $('body').html('加载错误');
            });
    }
}, 100);



